import React from 'react';
import ReactDOM from 'react-dom';
export function ChessboardExternals(externals = {}) {
    if (typeof window !== 'undefined') {
        window.React = React;
        window.ReactDOM = ReactDOM;
        Object.keys(externals)
            .forEach((e) => {
            window[e] = externals[e];
        });
    }
}
